@mixin size($a,$b: $a) {
  width: $a;
  height: $b; }

@mixin placeholder {
  ::-webkit-input-placeholder {
    @content; }
  ::-moz-placeholder {
    @content; }
  ::-moz-placeholder {
    @content; }
  ::-ms-input-placeholder {
    @content; } }

// mixing
@mixin fz($sizeValue: 1) {
  font-size: $sizeValue * 16 * 1px;
  font-size: $sizeValue * 1rem; }

//Mediaqueri
@mixin MQ($canvas) {
  @if $canvas == S {
    @media (min-width: $S) {
      @content; } }

  @else if $canvas == M {
    @media (min-width: $M) {
      @content; } }

  @else if $canvas == L {
    @media (min-width: $L) {
      @content; } }

  @else if $canvas == SL {
    @media (min-width: $SL) {
      @content; } }

  @else if $canvas == XL {
    @media (min-width: $XL) {
      @content; } } }

@mixin MQMAX($canvas) {
  @if $canvas == S {
    @media (max-width: $S) {
      @content; } }

  @else if $canvas == M {
    @media (max-width: $M) {
      @content; } }

  @else if $canvas == SL {
    @media (max-width: $SL) {
      @content; } }

  @else if $canvas == L {
    @media (max-width: $L) {
      @content; } }

  @else if $canvas == XL {
    @media (max-width: $XL) {
      @content; } } }

//Boton fantasma
@mixin column($percentage, $float-direction:left) {
  width: 100% * $percentage;
  float: $float-direction; }

//Boton fantasma
@mixin btnGhost {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  text-indent: 200%;
  white-space: nowrap;
  font-size: 0;
  opacity: 0;
  cursor: pointer; }
//Boton estados
@mixin press {
  &:hover, &:active, &:focus {
    @content; } }

// Heading site
@mixin header {
  h1, h2, h3, h4, h5, h6 {
    @content; } }
@mixin heading {
  h2, h3, h4, h5, h6, p, b, small {
    @content; } }

@mixin clearfix-after {
  clear: both; }

@mixin font-smoothing {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@mixin center($xy:xy) {
  @if $xy == xy {
    left: 50%;
    top: 50%;
    bottom: auto;
    right: auto;
    transform: translateX(-50%) translateY(-50%); }

  @else if $xy == x {
    left: 50%;
    right: auto;
    transform: translateX(-50%); }

  @else if $xy == y {
    top: 50%;
    bottom: auto;
    transform: translateY(-50%); } }

@mixin trans($ang, $scx, $scy, $wx, $wy, $x, $y) {
  transform: rotate($ang) scale($scx, $scy) skew($wx, $wy) translate($x, $y);
  -webkit-transform: rotate($ang) scale($scx, $scy) skew($wx, $wy) translate($x, $y);
  -moz-transform: rotate($ang) scale($scx, $scy) skew($wx, $wy) translate($x, $y);
  -o-transform: rotate($ang) scale($scx, $scy) skew($wx, $wy) translate($x, $y);
  -ms-transform: rotate($ang) scale($scx, $scy) skew($wx, $wy) translate($x, $y); }

@mixin pos($x, $y) {
  top: $y;
  left: $x;
  background-position-x: $x * -1;
  background-position-y: $y * -1; }

