.title,
.subtitle,
.paragraf {
    display: block; }

.paragraf {
    &_em2 {
        p {
            @include fz(1.5); } } }

.title-header {
	@include header {
		text-transform: uppercase;
		font-weight: 900;
		@include fz(3.5); } }

.title-intro h2,
.title-intro h1 {
	font-size: $h1-font-size; }

//.Color-site
.cl--primary {
	color: $primary;
	@include header {
		color: $primary; } }
.cl--second {
	color: $second;
	@include header {
		color: $second; } }
svg.cl--second {
	fill: $second; }

.cl--tertiary {
	color: $tertiary;
	@include header {
		color: $tertiary; } }

//.Color
.cl--white {
	color: $white;
	@include header {
		color: $white; } }

.cl--blue {
	color: $blue;
	@include header {
		color: $blue; } }

.cl--darkgray {
    color: $darkGray; }
.cl--gainsboro {
    color: $gainsboro; }
.cl--gray {
    color: $gray; }
.cl--yellow {
	color: $yellow; }
//.size
.txt_size--max {
	@include fz(3.5);
	line-height: 4.9rem; }
.txt_size--med {
	@include fz(1.5); }
.txt_size--min {
	font-size: 14px; }
.txt_size--small {
	font-size: 10px; }
.h1 {
	@include fz(3.5);
	line-height: 4.9rem; }

//.size
.txt_sm {
	@include fz(2); }
.txt_md {
	@include fz(2.5); }
.txt_lg {
	@include fz(3); }
.txt_xl {
	@include fz(3.5); }
//.tag_data
.txt {
    &_font {
        &--primal {
            font-family: $primary-font; }
        &--second {
            font-family: $second-font; } }
    &_regular {
        font-weight: 500; }
    &_bold,
    &_weight {
        &--700 {
            font-weight: 700;
            @include header {
                font-weight: 700; } }
        &--900 {
            font-weight: 900;
            @include header {
                font-weight: 900; }
            p {
                font-weight: 900; } } }
    &_italic {
        font-style: italic; }

    &_center {
        text-align: center; }
    &_upper {
        text-transform: uppercase; }
    &_capital {
        text-transform: capitalize; }
    &_right {
        text-align: right; }
    &_shadow {
        text-shadow: 0px 2px 2px ($black); }
    &_lineh {
        &--em1 {
            line-height: 1.6rem; }
        &--pf {
            line-height: 2rem; }
        &--em2 {
            line-height: 2.7rem; } }
    &_size {
        &--em1 {
            @include fz(1.5);
            li,
            p,
            b,
            span {
                @include fz(1.5); }
            @include heading {
                @include fz(1.5); } }
        &--em2 {
            @include fz(1.8);
            li,
            p,
            b,
            span {
                @include fz(1.8); }
            @include heading {
                @include fz(1.8); } }
        &--em3 {
            @include fz(3);
            li,
            p,
            b,
            span {
                @include fz(3); }
            @include heading {
                @include fz(3); } }
        &--em4 {
            @include fz(4);
            li,
            p,
            b,
            span {
                @include fz(4); }
            @include heading {
                @include fz(4); } }
        &--em5 {
            @include fz(5);
            li,
            p,
            b,
            span {
                @include fz(5); }
            @include heading {
                @include fz(5); } }
        &--em6 {
            @include fz(6);
            li,
            p,
            b,
            span {
                @include fz(6); }
            @include heading {
                @include fz(6); } }
        &--em7 {
            @include fz(7);
            li,
            p,
            b,
            span {
                @include fz(7); }
            @include heading {
                @include fz(7); } } } }
