.box-shadow {
	box-shadow: $boxShadow;
	&-top {
		box-shadow: $boxShadowTop; } }


// BOX LIST SEARCH
.box-list-search {
	@include MQ(L) {
		width: 80%;
		margin: 0 auto; }
	.list {
		li {
			padding: 0px 6px;
			width: 100%;
			@include MQ(L) {
				width: 18%; } } } }


// BOX ABOUT
.box {
	&-about {
		.paragraf {
			p {
				font-size: $p-font-paragraf;
				margin-bottom: $p-parparagraf-mb; } }
		&-vision {
			padding: 120px 0px;
			.title {
				@include heading {
					font-size: $h-font-size;
					color: $white;
					margin-bottom: 22px;
					font-weight: 900;
					text-align: center;
					text-transform: uppercase; } }
			.paragraf {
				padding: 0 12px;
				p {
					font-family: $p-font-paragraf;
					color: $white;
					text-align: justify; } } } } }

// BOX MENU RIGHT
.box {
	&-menu-right {
		width: 90%;
		.title:after {

			content: "";
			display: inline-block;
			width: 86%;
			height: 2px;
			display: block;
			background-color: $second;
			vertical-align: middle; }

		.title {
			@include heading {
				text-transform: uppercase;
				margin-bottom: 12px;
				@include fz(1.2); } }

		.list {
			&:after {
				container: "";
				width: 86%;
				height: 5px;
				display: block;
				background-color: red; }
			.menu {
				background-color: $gainsboro;
				box-shadow: $shadow;
				border-radius: 0;
				li {
					border: none;
					a {
						color: $black; } } } } } }

// BOX
.box-benefist {
	.list-group {
		.list-group-item {
			border: none; } }
	.btn_alt--product {
		text-decoration: none;

		.circle {
			width: 100px;
			height: 100px;
			background-color: transparent;
			border: 3px solid $tertiary;
			svg {
				fill: $tertiary; }
			&.right {
				margin: 14px 55%; }
			&.left {
				margin: 14px 0; } }
		@include press {
			.circle {
				background-color: $second;
				border: 3px solid $second;
				svg {
					fill: $white; } } } } }
// BOX form quote
.box {
	&-form {
		&-quote {
			background: $silver;
			padding: 44px 30px;
			.title {
				@include heading {
					font-weight: 600;
					text-transform: uppercase;
					@include fz(1.5); } } } } }

// BOX HOTELS LIST
.box {
	&-hotels-list {
		figure {
			img {
				width: 90%; } }
		.title {
			@include heading {
				font-weight: 600;
				text-transform: uppercase;
				@include fz(1.5); } }
		.subtitle {
			@include heading {
				@include fz(2); } }
		.paragraf {
			p {
				@include fz(1.2); } } } }

// BOX HOTELS
.box {
	&-hotels {
		.title {
			@include heading {
				font-weight: 600;
				text-transform: uppercase;
				@include fz(1.5); } }
		.subtitle {
			@include heading {
				@include fz(2); } }
		.paragraf {
			p {
				@include fz(1.2); } } } }

// BOX LIST
.box {
	&-list {
		.list-inline {
			&-item {
				background: $silver;
				padding: 12px;
				font-size: 22px;
				width: 200px;
				margin-bottom: 12px;
				box-shadow: $shadow;
				vertical-align: top;
				@include MQMAX(SL) {
					width: 30%; }
				@include MQMAX(L) {
					width: 160px; } } } } }
.box {
	&-list-policies {
		.list-inline {
			&-item {
				padding: 12px;
				.box-item {
					background: $silver;
					margin-bottom: 10px;
					box-shadow: $shadow;
					vertical-align: top;
					width: 100%;
					@include MQMAX(SL) {
						width: 30%; }
					@include MQMAX(L) {
						width: 160px; }
					@include heading {
						font-weight: 700;
						text-align: center;
						@include fz(1.3); }
					&-min {
						padding: 5px 0px;
						background-color: $second;
						cursor: pointer;
						img {
							height: 25px; }
						@include press {
							background-color: $tertiary; } }
					&-max {
						padding: 60px 33px; } } } } } }
// BOX Foundation
.box {
	&-foundation {
		.title {
			@include heading {
				font-weight: 900;
				text-transform: uppercase;
				text-align: center;
				@include fz(2.3); } }

		.paragraf {
			p {
				@include fz(1.2); } } } }

//BOX CALL TO ACTION
.box {
	&_call-to-action {
		.paragraf {
			@include fz(1.9);
			margin-bottom: 9px; }
		.subtitle {
			@include fz(1.9);
			margin-bottom: 9px; }
		>.title {
			font-weight: 900;
			text-transform: uppercase;
			@include fz(3.5);
			margin-bottom: 9px; }
		.call {
			background-color: $tertiary;
			border-radius: 25px;
			padding: 5px 22px;
			display: inline-block;
			color: $white;
			margin-bottom: 12px;
			.title {
				color: $white;
				font-weight: 900;
				@include fz(3); } } } }

