// BOX TITLE
.box {
	&-head {
		.title {
			@include fz(3.5);
			font-weight: 900; } } }

// BOX PROD
.box {
	&--prod {
		background: $silver url(#{$imagePath}bg-prod.png)repeat center center/112%;
		padding: 150px 0;
		@include MQMAX(L) {
			background: $silver url(#{$imagePath}bg-prod.png)repeat center center/200%; } } }

// BOX Footer
.box {
	&--footer {
		background: $second url(#{$imagePath}bg-prod.png)repeat center center/112%;
		padding: 150px 0;
		@include MQMAX(L) {
			background: $second url(#{$imagePath}bg-prod.png)repeat center center/200%; } } }

// BOX SLIDER HOME
.box {
	&-slider-home {
		background-image: url(#{$imagePath}bgi-slider.png);
		background-repeat: repeat-y;
		background-size: 73%;
		background-position-x: center;
		background-position-y: -425px;
		overflow: hidden;
		.slider {
			&-content {
				.title {
					@include heading {
						font-size: 45px;
						font-weight: 900;
						@include MQMAX(SL) {
							//font-size: 38px
							@include fz(2.7);
							margin-bottom: 12px; } } }
				.subtitle {
					@include heading {
						font-size: 35px;
						@include MQMAX(SL) {
							margin-bottom: 12px;
							@include fz(1.6); } } }
				.paragraph {
					@include heading {
						font-size: 19px;
						@include MQMAX(SL) {
							@include fz(0.95); } } } }
			&-img {
				margin-bottom: -209px;
				@include MQMAX(SL) {
					width: 50%;
					margin: 0 auto -109px; } } } } }
// BOX PARTNERS
.box {
	&_slider {
		&--partners {
			padding: 44px 0;
			background-color: $white;
			box-shadow: $shadow;
			border-radius: 12px;
			width: 100%;
			display: inline-block;
			position: relative;
			.list-group {
				.box_logo--partners {
					padding: 0 12px;
					&:first-child {
						padding-left: 44px; }
					&:last-child {
						padding-right: 44px; } } } } } }

// Box Footer Content
.box-content-footer {
	a span {
		text-transform: uppercase; } }


.element {
	position: absolute;
	display: inline-block;
	z-index: 1;
	@include MQMAX(L) {
		transform: rotate(90deg); }
	@include MQMAX(SL) {
		position: relative; }
	&-top {
		top: 0; }
	&-bottom {
		bottom: 0; }
	&-left {
		left: 55px;
		@include MQMAX(XL) {
			left: -55px; }
		@include MQMAX(SL) {
			left: inherit;
			top: 160px;
			width: 60%;
			display: grid;
			margin: -54px auto; }
		&--child {
			left: 155px;
			@include MQMAX(XL) {
				left: -45px; }
			@include MQMAX(SL) {
				left: -55px; } } }
	&-right {
		right: 55px;
		top: 0;
		@include MQMAX(XL) {
			right: -45px;
			top: 0; }
		@include MQMAX(SL) {
			right: inherit;
			top: 0;
			width: 195px;
			display: grid;
			margin: auto; }
		&--child {
			right: 155px;
			@include MQMAX(XL) {
				right: -45px; }
			@include MQMAX(SL) {
				right: -55px; } } }
	&--child {
		display: inline-block;
		&-top {
			width: 250px; }
		&-bottom {
			width: 350px; }
		img {
			width: 100%; } } }
// PETS DECO HOME
.pets {
	position: absolute;
	display: inline-block;
	z-index: 1;
	@include MQMAX(L) {
		display: none; }
	&-top {
		top: -175px;
		@include MQMAX(XL) {
			top: -45px; } }
	&-bottom {
		bottom: -170px; }
	&-left {
		left: 50px;
		@include MQMAX(XL) {
			left: -45px; }
		@include MQMAX(SL) {
			left: -55px; }
		&--child {
			left: -15px;
			@include MQMAX(XL) {
				left: -45px; }
			@include MQMAX(SL) {
				left: -55px; } } }
	&-right {
		right: 55px;
		@include MQMAX(XL) {
			right: -45px; }
		@include MQMAX(SL) {
			right: -55px; }
		&--child {
			right: -5px;
			@include MQMAX(XL) {
				right: -45px; }
			@include MQMAX(SL) {
				right: -55px; } } }
	&--child {
		display: inline-block;
		&-top {
			width: 250px; }
		&-bottom {
			width: 350px; }
		img {
			width: 100%; } } }
img.img-max {
	@include MQ(XL) {
		height: 321px; } }
